import React , {useState } from 'react';
import CardItems from '../CardItem/Card';
import Card from '../Cards/Card';
import Footer from '../Footer/Footer';
import HeroSection from '../HeroSection/HeroSection';
import './Body.css';
import styled from "styled-components";
import { useMediaQuery } from "react-responsive";
import { Link } from "react-router-dom";
import {deviceSize} from '../responsive/index';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {   faYoutube , faYoutubeSquare } from "@fortawesome/free-brands-svg-icons";
import Services from '../Services/Services';


const ProgrammingLanguageContainer = styled.div`
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
`;

const SocialIcon = styled.div`
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
  color: #8a8a8a;
  font-size: 70px;
  cursor: pointer;
  transition: background-color, 200ms ease-in-out;

  &:not(:last-of-type) {
    margin-right: 10px;
  }

  &:hover {
    color: #777777;
  }

  @media screen and (max-width: ${deviceSize.mobile}px) {
    font-size: 17px;
  }
`;

const ContainerText = styled.h1`
  color:black;
  font-size:20px;
  display: flex;
  justify-self: center;
  align-self: center;
`;

const WrapperCardWrapIconWithText = styled.div`
    display: flex;
    flex-wrap:wrap;
    align-items: center;
    white-space:nowrap;
    justify-content: center;
    width: 100%;
    height: ${({ size }) =>
    typeof size === "string" ? size : `${size}%`};
    background:white;
`;

const CardWrapIconWithText = styled.div`
    border-radius:10%;
    box-shadow: 12px 12px 2px 1px rgba(0, 0, 255, .2);
    width:200px;
    display: flex;
    margin: 10px;
    align-items:center;
    justify-content: center;
    padding:30px;
    background: #f3f3f3;
`;

const Title = styled.h1`
  font-weight: 900;
  color: black;
  font-size:25px;
  margin-right: 10px;
`;

const FLink = styled.a`
  text-decoration: none;
  color: #6f6f6f;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;

  &:not(:last-of-type) {
    margin-bottom: 8px;
  }
`;




const Body = () => {
    
    const OpenYoutube = () =>{
        window.location.href='https://www.youtube.com/channel/UCDuvG5eIUF6a9UIqPM8i11A';
   };
    
    const isMobile = useMediaQuery({ maxWidth: deviceSize.mobile });

    console.log("This is my size "+isMobile)
   
    return (
        <>
          <HeroSection/>
          <Services/>
          <Link onClick ={OpenYoutube} >
          <SocialIcon>
             <Title>
                 Subscribe To Youtube Channel for software tutorials
            </Title> 
            <FontAwesomeIcon icon={faYoutube} color ="red" />
          </SocialIcon>
          </Link>
          <Card/>
          <ProgrammingLanguageContainer>
             <ContainerText>Programming Languages and Framework i Use Include</ContainerText> 
             {!isMobile &&<WrapperCardWrapIconWithText size = {50}>
               
               <CardWrapIconWithText>
                   <ContainerText>Java</ContainerText>                   
               </CardWrapIconWithText>


               <CardWrapIconWithText>
                   <ContainerText>Flutter</ContainerText>                   
               </CardWrapIconWithText>

               <CardWrapIconWithText>
                   <ContainerText>Native Android</ContainerText>                   
               </CardWrapIconWithText>
             
               <CardWrapIconWithText>
                   <ContainerText>React</ContainerText>                   
               </CardWrapIconWithText>

               
               <CardWrapIconWithText>
                   <ContainerText>Node.js</ContainerText>                   
               </CardWrapIconWithText>
               
               <CardWrapIconWithText>
                   <ContainerText>JavaScript</ContainerText>                   
               </CardWrapIconWithText>
              
             </WrapperCardWrapIconWithText>}
            
             {isMobile &&<WrapperCardWrapIconWithText size = {50}>
               
               <CardWrapIconWithText>
                   <ContainerText>Java</ContainerText>                   
               </CardWrapIconWithText>


               <CardWrapIconWithText>
                   <ContainerText>Flutter</ContainerText>                   
               </CardWrapIconWithText>

               <CardWrapIconWithText>
                   <ContainerText>Native Android</ContainerText>                   
               </CardWrapIconWithText>
             
               <CardWrapIconWithText>
                   <ContainerText>React</ContainerText>                   
               </CardWrapIconWithText>

               
               <CardWrapIconWithText>
                   <ContainerText>Node.js</ContainerText>                   
               </CardWrapIconWithText>
                      
               <CardWrapIconWithText>
                   <ContainerText>JavaScript</ContainerText>                   
               </CardWrapIconWithText>
               

             </WrapperCardWrapIconWithText>}
          </ProgrammingLanguageContainer>
          <Footer/>
       </>
    )
}

export default Body
