import React , {useEffect , useState }from 'react'
import {
    InnerPageContainer,
    PageContainer,
  } from "../Containers/index";
import { ServiceCard } from './ServiceCard';
import styled from "styled-components";



const DummyArray =[
    { id: 1, title: "Using The Core Concepts Of Java I Will Create Enterprise Solution For both your web and mobile applications", 
  thumbnailUrl: "images/java.jpg", "rate": null,
  "rating": null, specialist: { "id": 1, "fullName": "Java Full Stack"}
  },
  { id: 2, title: "I Can Make Restful Apis For Your Solution , Web ResfulApi Helps Your Frontend Communicate with backend ", 
    thumbnailUrl: "images/api.jpg", rate: null,
    rating: null, specialist: { "id": 1, "fullName": "WebApi Development"}},
    { id: 3, title : "I Will Make Quality WebSite For Your Enterprise Using React and Web Technologies", 
      "thumbnailUrl": "images/webdev.jpg", rate: null,
      rating : null, specialist: { "id": 1, "fullName": "FrontEnd Development" }},
      { id: 4, title: "Using Databases Like , MongoDb , PostGresQl , MySql , And Firebase , I Will Handle Database Structures For Your Enterprise", 
        thumbnailUrl: "images/database.jpg", "rate": null,
        rating:null,specialist: { "id": 1, "fullName": "Mark brone" }}
  ]

  const BackgroundFilter = styled.div`
  width: 100%;
  height: 100%;
  background-color: rgba(38, 70, 83, 0.9);
  display: flex;
  flex-direction: column;
`;

const ServicesContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: url(${'images/servicebackground.jpg'}) no-repeat;
  object-fit: contain;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  
`;

const Title = styled.h1`
  font-weight: 900;
  color: #fff;

 
`;

// @media screen and (max-width: ${deviceSize.mobile}px) {
//     font-size: 25px;
//   }

const ServicesWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-items: center;
  justify-content: center;
`;

  export function Services(props) {

  //Initially Offered Services is an empty Array
  const [offeredServices, setServices] = useState([]);

  //We trigger the fetch in use effect
  //Because use effect is called when the component is mounted and happen once in the component life cycle
  useEffect(() => {
    // fetchServices();
    setServices(DummyArray)
  }, []);
    return (
      <PageContainer>
        <InnerPageContainer>
            <ServicesContainer>
             <BackgroundFilter>
             <Title>Software services i render include</Title>
             <ServicesWrapper>
                 { offeredServices.map((service, idx) => (
                 <ServiceCard key={idx} {...service} />
            ))}  
             </ServicesWrapper>
              </BackgroundFilter> 
            </ServicesContainer>
        </InnerPageContainer>
      </PageContainer>
    );
  }
  

export default Services
