import React , {useState} from 'react';
import Button from '../Button/Button';
import './HeroSectio.css';
import {Link} from 'react-router-dom';


const HeroSection = () => {

    //const[Myfile , setFile ]= useState('files/matrend-release.apk');

    const OpenYoutube = () =>{
         window.location.href='https://www.youtube.com/channel/UCDuvG5eIUF6a9UIqPM8i11A';
    };

    const OpenPlayStore = () => {
        window.location.href = 'https://play.google.com/store/apps/details?id=sample.meepo.nekocode.joe.j_pub';
    };

    return (
        <div className = 'hero-container'>
            <video src = "/videos/intro.mp4" autoPlay loop muted/>
            <h1 className ="welcome">Etim Joseph Okon</h1>
            <p className = "my-text">About Me (SoftWare Developer)</p>
            <div className ="hero-btns">
                  <Button className ="btn" buttonStyle = "btn--outline" buttonSize = "btn--large" onClick = {OpenYoutube}>My Youtube</Button>
                  <Button className ="btn" buttonStyle = "btn--primary" buttonSize = "btn--large" onClick={OpenPlayStore}>My Work</Button>
                  <br></br>
                  {/* <input type="file" id="matrendApk" value ='matrend-release.apk'/> */}
                  <li className="me_card">
                 <Link className = "me_link" to = '/about/'>
                       <figure className ="me_figure" data-category="profile">
                                <img className ="me_image" src ='images/me.jpg' alt = "me"/>
                       </figure>       
                 </Link>
                 </li>
                 <p className="intro1">
                   Name:   Joseph Etim
                </p>
                <p className="introemiail">
                    Email:  jetim186@gmail.com
                </p>
            
            </div>
        </div>
    )
}

export default HeroSection
