import React from 'react';
import CardItems from '../CardItem/Card';
import './Card.css';
import Button from '../Button/Button';

const Card = () => {

    const OpenPlayStore = () => {
        window.location.href = 'https://play.google.com/store/apps/details?id=sample.meepo.nekocode.joe.j_pub';
    };


    return (
        <>
            <div className="cards">
            <div className="wapTex">
                <p className="intro">
                    Location: Nigeria ,Lagos
                </p>
            </div>
               
                <h1 className="myText">My Software Skills Include: </h1>
                <div className="card_container">

                    <div className="cards_wrapper">
                        <ul className="my_items">
                            <CardItems
                                src="images/android.jpg"
                                text="Android App Development"
                                path="/services"
                                label="Adventure"
                            />
                            <CardItems
                                src="images/flutter.png"
                                text="Flutter/Dart Developer"
                                path="/services"
                                label="Adventure"
                            />
                        </ul>
                    </div>
                    <div className="cards_wrapper">
                        <ul className="my_items">
                            <CardItems
                                src="images/webdev.png"
                                text="Web Developer"
                                path="/services"
                                label="Adventure"
                            />
                            <CardItems
                                src="images/Backend.jpg"
                                text="RestApi/BackEnd"
                                path="/services"
                                label="Adventure"
                            />
                        </ul>

                    </div>
                </div>
                <div className="AboutMatrend">
                    <p className="AboutMatrendP">Etim , Joseph Okon The Software Developer Behind the Matrend App which is currently on playstore , Good At Collaborating with Team Members and Passionate about creating new Things, Proficient in Java and Web Technologies.</p>

                    <div className="btnn">
                        <Button className="btn" buttonStyle="btn--outline" buttonSize="btn--large" onClick={OpenPlayStore}>Download Matrend App</Button>
                    </div>
                
                    <h1 className="AboutMatrendH">Features Of The Mobile App:</h1>
                    <p className="AboutMatrendP">-OTP Phone Number Verification</p>
                    <p className="AboutMatrendP">-Email And Password Authentication</p>
                    <p className="AboutMatrendP">-Firebase Database Queries</p>
                    <p className="AboutMatrendP">-RestApi Communications</p>
                    <p className="AboutMatrendP">-Video Calling , Voice Calling Features</p>
                    <p className="AboutMatrendP">-GeoGraphical Search and Ecommerce Features</p>
                    <p className="AboutMatrendP">-One To One Chat</p>
                    <p className="AboutMatrendP">-Realm Database Queries</p>
                    <p className="AboutMatrendP">-Google Map and File Sharing System</p>
                </div>
            </div>
        </>

    )
}

export default Card
